const PageCover = React.forwardRef((props, ref) => {
  return (
    <div className="page page-cover" ref={ref} data-density="hard">
      <div className="page-content">
        <h2>{props.children}</h2>
        <hr></hr>

        <div class="alert alert-dismissible alert-danger" >
          {/* <button type="button" class="close" data-dismiss="alert">&times;</button> */}
          <strong>ANTIFRAGILE</strong> Please handle with care!
        </div>

      </div>
    </div>
  );
});

const PageEnd = React.forwardRef((props, ref) => {
  return (
    <div className="page page-cover" ref={ref} data-density="hard">
      <div className="page-content">
        <h2>{props.children}</h2>
        <hr></hr>
      </div>
    </div>
  );
});

const Page = React.forwardRef((props, ref) => {

  if (props.number === 1)
    return (
      <div className="page" ref={ref}>
        <div className="page-content">
          <div className="page-image" style={{ backgroundImage: "url(" + Image0 + ")", backgroundSize: 'cover' }} > </div>
        </div>
      </div>
    )

  if (props.number === 2)
    return (
      <div className="page" ref={ref}>
        <div className="page-content">
          <div className="container" >
            <div className="row">
              <div class="col-sm">
                <blockquote class="blockquote text-right" style={{ margin: "13%" }}>
                  <p class="mb-0">⚡ τὰ δὲ πάντα οἰακίζει Κεραυνός <br></br>  All things are ruled by Lightning</p>
                  <footer class="blockquote-footer"><p class="lead">Heraclitus in <cite title="Source Title">Fragment 64</cite></p></footer>
                </blockquote>
              </div>
            </div>
          </div>
        </div>
      </div>
    )

  if (props.number === 3)
    return (
      <div className="page" ref={ref}>
        <div className="page-content">
          <div className="jumbotron page-image" style={{ margin: "4%", backgroundColor:"#000000", backgroundImage: "url(" + Image1 + ")" }} > </div>
        </div>
      </div>
    )

  if (props.number === 5)
    return (
      <div className="page" ref={ref}>
        <div className="page-content">
          <div className="jumbotron page-image" style={{ margin: "4%", backgroundColor:"#000000", backgroundImage: "url(" + Image2 + ")" }} > </div>
        </div>
      </div>
    )


  if (props.number === 7)
    return (
      <div className="page" ref={ref}>
        <div className="page-content">
          <div className="jumbotron page-image" style={{ margin: "4%", backgroundColor:"#000000", backgroundImage: "url(" + Image3 + ")" }} > </div>
        </div>
      </div>
    )

  return (
    <div className="page" ref={ref}>
      <div className="page-content">
        <h2 className="page-header">Page header - {props.number}</h2>
        <div className="page-text">{props.children}</div>
        <div className="page-footer">{props.number + 1}</div>
      </div>
    </div>
  );
});

export default class DemoBlock extends React.Component {
  constructor(props) {
    super(props);

    const pages = [<PageCover key={0} pos="top">G.'s Scrapebook</PageCover>];

    let pageNum = 0;
    for (let i = 0; i < 8; i++) {
      pageNum++;
      if (pageNum > 8) pageNum = 1;
      pages.push(<Page key={i + 1} image={(pageNum) + ".jpg"} number={i + 1}>Lorem ipsum dolor sit amet, consectetur adipiscing elit. In cursus mollis nibh, non convallis ex convallis eu. Suspendisse potenti. Aenean vitae pellentesque erat. Integer non tristique quam. Suspendisse rutrum, augue ac sollicitudin mollis, eros velit viverra metus, a venenatis tellus tellus id magna. Aliquam ac nulla rhoncus, accumsan eros sed, viverra enim. Pellentesque non justo vel nibh sollicitudin pharetra suscipit ut ipsum. Lorem ipsum dolor sit amet, consectetur adipiscing elit. In cursus mollis nibh, non convallis ex convallis eu. Suspendisse potenti. Aenean vitae pellentesque erat. Integer non tristique quam. Suspendisse rutrum, augue ac sollicitudin mollis, eros velit viverra metus, a venenatis tellus tellus id magna.</Page>);
    }

    pages.push(<PageEnd key={101} pos="bottom">So Long, and Thanks for All the Fish 🐬</PageEnd>);

    this.state = {
      page: 0,
      totalPage: 0,
      orientation: 'landscape',
      state: 'read',
      pages: pages
    };
  }

  nextButtonClick = () => {
    this.flipBook.getPageFlip().flipNext();
  }

  prevButtonClick = () => {
    this.flipBook.getPageFlip().flipPrev();
  }

  onPage = (e) => {
    this.setState({
      page: e.data,
    });
  }

  onChangeOrientation = (e) => {
    this.setState({
      orientation: e.data,
    });
  }

  onChangeState = (e) => {
    this.setState({
      state: e.data,
    });
  }

  componentDidMount() {
    this.setState({
      totalPage: this.flipBook.getPageFlip().getPageCount(),
    });
  }

  render() {
    return (
      <div>
        <div className="container-md"  style={{paddingTop:"1%", backgroundImage: "url(" + wood + ")", position: "relative" }}>
          <HTMLFlipBook
            width={550}
            height={625}
            size="stretch"
            minWidth={315}
            maxWidth={1000}
            minHeight={400}
            maxHeight={1533}
            maxShadowOpacity={0.5}
            showCover={true}
            mobileScrollSupport={true}

            onFlip={this.onPage}
            onChangeOrientation={this.onChangeOrientation}
            onChangeState={this.onChangeState}

            className=" html-book demo-book"
            style={{ backgroundImage: "url(images/background.jpg)" }}

            ref={(el) => this.flipBook = el}
          >
            {this.state.pages}
          </HTMLFlipBook>
          <br></br>

        </div>

        {/* <div className="container mt-3">
          <div className="row">
            <div className="col-md-6">
              <button
                type="button"
                className="btn btn-info btn-sm btn-prev"
                onClick={this.prevButtonClick}
              >
                Previous page
              </button>
                [<span>{this.state.page + 1}</span> of <span>{this.state.totalPage}</span>]
              <button
                type="button"
                className="btn btn-info btn-sm btn-next"
                onClick={this.nextButtonClick}
              >
                Next page
              </button>
            </div>
            <div className="col-md-6">
              State: <i>{this.state.state}</i>, orientation: <i>{this.state.orientation}</i>
            </div>
          </div>
        </div> */}
      </div>
    );
  }
}




